body {
	background: #eee;
	font-family: "Nunito", Arial, sans-serif;
}

#main {
	padding: 0px;
	box-sizing: border-box;
	width: 60%;
	height: 100%;
}

#main h1 {
	color: #444;
	text-align: center;
}

#book-list {
	padding: 0;
}

#book-list li {
	display: inline-block;
	margin: 12px;
	padding: 10px;
	border-radius: 4px;
	border: 1px solid #880e4f;
	box-shadow: 1px 2px 3px rgba(0, 0, 0, 0.3);
	cursor: pointer;
	color: #880e4f;
}

form#add-book {
	background-color: #fff;
	padding: 20px;
	position: fixed;
	left: 0;
	bottom: 0;
	width: 400px;
}

form#add-book .field {
	display: grid;
	grid-template-columns: 1fr 1fr;
	grid-gap: 10px;
}

form#add-book .field label {
	text-align: right;
	padding: 6px;
}

form#add-book .field select,
form#add-book .field input {
	margin: 4px;
	padding: 6px;
	box-sizing: border-box;
}

form#add-book button {
	color: #fff;
	font-size: 2em;
	background: #ad1457;
	border: 0;
	padding: 0 16px 4px;
	border-radius: 50%;
	cursor: pointer;
	position: absolute;
	bottom: 10px;
	left: 10px;
}

#book-details {
	position: fixed;
	top: 0;
	right: 0;
	width: 40%;
	height: 100%;
	background: #ad1457;
	padding: 30px;
	overflow: auto;
	box-shadow: -2px -3px 5px rgba(0, 0, 0, 0.3);
	box-sizing: border-box;
	color: #fff;
}
.navbar ul {
	margin: 0;
	padding: 0;
	list-style: none;
}

.navbar ul li {
	display: inline-block;
	margin: 0 5px;
}

.navbar ul li a {
	color: #444;
}

#admin h2 {
	text-align: center;
	color: #444;
}
#admin .authors-list {
	margin-top: 60px;
}

#admin .author {
	margin-bottom: 15px;
	border-bottom: 1px solid #ad1457;
}

#admin .author .delete-author-btn {
	color: #ad1457;
	text-decoration: underline;
	cursor: pointer;
}

#admin .author .fa-trash {
	color: #ad1457;
	cursor: pointer;
}
